.ticket__wrapper {
  background-color: #F8F9FC;
  margin-top: 235px; }

.ticket__inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }

.ticket__title {
  padding-top: 120px; }

.ticket__tabs {
  display: flex;
  margin-top: 53px;
  margin-bottom: 39px; }

.tabs__item {
  padding-right: 46px;
  display: block;
  border-bottom: 2px solid rgba(78, 78, 78, 0.2);
  padding-bottom: 20px; }
  .tabs__item:last-child {
    padding-right: 0; }

.tabs__link {
  font-size: 20px;
  line-height: 150%;
  padding-bottom: 23px;
  color: rgba(16, 38, 84, 0.5);
  margin-right: 20px;
  /*&:hover{
      color: #102654;
      border-bottom: 2px solid #D10A42;
    }*/ }
  .tabs__link:last-child {
    margin-right: 0; }
  .tabs__link--active {
    color: #102654;
    border-bottom: 2px solid #D10A42; }
  .tabs__link--noactive {
    color: rgba(16, 38, 84, 0.5); }

.tabs__info--noactive {
  display: none; }

.tabs__info--active {
  display: flex; }

.ticket-price__item {
  background-color: #ffffff;
  margin-right: 40px;
  border: 10px solid transparent; }
  .ticket-price__item:hover {
    border: 10px solid transparent;
    border-image: linear-gradient(150deg, #D10A42, #00359F);
    border-image-slice: 1; }
    .ticket-price__item:hover .btn__ticket-price {
      background: linear-gradient(111.72deg, #ED0233 17.6%, #003EB7 95.31%);
      color: #ffffff;
      border: none;
      padding: 29px 47px; }

.ticket-price__info {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 68px 77px; }

.ticket-price__name {
  font-size: 26px;
  line-height: 112%;
  font-weight: 800; }

.ticket-price__lessons {
  font-size: 16px;
  line-height: 150%;
  color: #495A7D;
  margin-top: -5px; }

.ticket-price__price {
  font-weight: 800;
  font-size: 60px;
