  color: #103AAE;
  display: flex;
  align-items: center;
  position: relative;
  z-index: 100; }
  .ticket-price__price img {
    padding-left: 14px; }
  .ticket-price__price:before {
    content: attr(data-price);
    position: absolute;
    top: -50%;
    height: 100%;
    font-weight: 800;
    font-size: 100px;
    line-height: 150%;
    color: #E8E8E8;
